.header{
  width: 100%;
  height: 50px;
  background-color: rgb(32,35,42);
  text-align: left;
  color: #fff;
  overflow: hidden;
  position: relative;
}
.header-logo{
  height: 50px;
  width: 50px;
  padding: 8px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  display: inline-block;
  position: absolute;
  left: 0;
  top: 0;
}
.header-title{
  width: 100%;
  display: inline-block;
  vertical-align: top;
  line-height: 50px;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  padding-left: 50px;
  padding-right: 180px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 1em;
}
.options:hover{
  color: #bbb;
}
.options:active{
  color: #999;
}
.options{
  position: absolute;
  right: 0;
  top: 0;
  cursor: pointer;
  height: 100%;
  color: #fff;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.options-text{
  line-height: 50px;
  display: inline-block;
  float: left;
  padding: 0 3px;
  font-size: 0.8rem;
}
.options-btn{
  display: inline-block;
  width: 50px;
  height: 50px;
  padding: 8px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: relative;
}
.options-btn:before{
  content: '';
  position: absolute;
  top: 14px;
  left: 14px;
  width: 20px;
  height: 0;
  border: 1px solid transparent;
  -webkit-box-shadow: 0 5px 0 0, 0 -5px 0 0;
  box-shadow: 0 10px 0 0, 0 15px 0 0, 0 5px 0 0;
}



