.figure-frame{
  -webkit-box-shadow: -1px 1px 5px 1px rgba(0,0,0,0.5);
          box-shadow: -1px 1px 5px 1px rgba(0,0,0,0.5);
  display: inline-block;
  padding: 10px 15px;
  max-width: 90%;
}
.figure-frame:hover{
  background-color: rgba(32,35,42,0.1);
}
.figure-container{
  position: relative;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.figure{
  width: 100%;
}
.figure-loading{
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
      -ms-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  height: 100px;
}
.figure-caption{
  text-align: left;
  font-size: 0.8rem;
  color: #555;
  font-family: sans-serif;
  margin: 0.5rem 1rem;
  line-height: 1.3;
}
.figure-n{
  font-style: italic;
  color: rgb(43,87,154);
  margin-right: 0.2rem;
}



